import React, { Component } from "react";
import { Steps } from "antd";

const Step = Steps.Step;

class Head extends Component {
  state = {
    current: "01"
  };

  handleClick = e => {
    console.log("click ", e);
    this.setState({
      current: e.key
    });
  };

  render() {
    return (
      <div style={{padding:"0 10%"}} className="head">
        <h1 style={{ textAlign: "center",margin:"40px 0 10px 0",fontWeight:"600"}}></h1>
     
        
        <Steps style={{margin:"10px 0 40px 0"}} current={this.props.step} >
          <Step title=""  />
          <Step title=""  />
          <Step title=""  />
          <Step title=""  />
        </Steps>
        {this.props.show? <div style={{padding:"0 44px"}} >
          <h3 style={{fontSize:"14px",fontWeight:"500",marginBottom:"20px"}}></h3>
          <p style={{lineHeight:"18px"}}>1</p>
          <p style={{lineHeight:"18px"}}>2</p>
          <p style={{lineHeight:"18px"}}>310~15</p>
          <p style={{lineHeight:"18px",marginBottom:"44px"}}>4info@collinstar.com.cn </p>
        </div>:""}
       
      </div>
    );
  }
}
export default Head;



